html {
  background-color: #520034;
}
html:before {
  position: absolute;
  width: 100vw;
  min-height: 100vh;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url(https://www.transparenttextures.com/patterns/graphy-dark.png);
  opacity: 0.3;
}
* {
  box-sizing: border-box;
}
::selection {
  color: #e91140;
}
body {
  width: 100vw;
  overflow-x: hidden;
}
body #barba-wrapper .hamburger {
  display: none;
  position: fixed;
  z-index: 1000;
  padding: 15px 15px;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  background-color: transparent;
  border: 0;
}
body #barba-wrapper .hamburger:focus {
  outline: none !important;
}
@media (max-width: 767px) {
  body #barba-wrapper .hamburger {
    display: inline-block;
  }
}
body #barba-wrapper .hamburger .hamburger-box {
  width: 30px;
  height: 18px;
  display: inline-block;
  position: relative;
}
body #barba-wrapper .hamburger .hamburger-box .hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -1px;
}
body #barba-wrapper .hamburger .hamburger-box .hamburger-inner,
body #barba-wrapper .hamburger .hamburger-box .hamburger-inner::before,
body #barba-wrapper .hamburger .hamburger-box .hamburger-inner::after {
  width: 30px;
  height: 2px;
  background-color: #e3ed26;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
body #barba-wrapper .hamburger .hamburger-box .hamburger-inner::before,
body #barba-wrapper .hamburger .hamburger-box .hamburger-inner::after {
  content: "";
  display: block;
}
body #barba-wrapper .hamburger .hamburger-box .hamburger-inner::before {
  top: -8px;
}
body #barba-wrapper .hamburger .hamburger-box .hamburger-inner::after {
  bottom: -8px;
}
